import {CREATE_ORDER_REQUEST,
    CREATE_ORDER_FAIL,
    CREATE_ORDER_SUCCESS,
    MY_ORDERS_FAIL,
    MY_ORDERS_SUCCESS,
    MY_ORDERS_REQUEST,
    ORDER_DETAILS_FAIL,
    ORDER_DETAILS_REQUEST,
    ORDER_DETAILS_SUCCESS,
    ALL_ORDERS_FAIL,
    ALL_ORDERS_REQUEST,
    ALL_ORDERS_SUCCESS,
    DELETE_ORDER_FAIL,
    DELETE_ORDER_REQUEST,
    DELETE_ORDER_SUCCESS,
    UPDATE_ORDER_FAIL,
    UPDATE_ORDER_REQUEST,
    UPDATE_ORDER_SUCCESS,
    CLEAR_ERRORS,
} from "../constants/orderConstant";
import axios  from "axios";

//create Order

export const createOrder = (order)=>async(dispatch)=>{
     
    try {
        dispatch({ type:CREATE_ORDER_REQUEST });
        const config = {
            headers:{
                'Content-Type':'application/json',
            },
        };

        const {data}= await axios.post("https://onlinemoviebooking-backend.herokuapp.com/api/v1/order/new",order,config);
        dispatch({type:CREATE_ORDER_SUCCESS,payload:data});

    }catch(error){

        dispatch({
            type:CREATE_ORDER_FAIL,
            payload:error.response.data.message,
        });
    }
    
};

//fetch my orders 

export const myOrders = (order)=>async(dispatch)=>{
     
    try {
        dispatch({ type:MY_ORDERS_REQUEST });
        const config = {
            headers:{
                'Content-Type':'application/json',
            },
        };

        const {data}= await axios.get("https://onlinemoviebooking-backend.herokuapp.com/api/v1/orders/me");
        dispatch({type:MY_ORDERS_SUCCESS,payload:data.orders});

    }catch(error){

        dispatch({
            type:MY_ORDERS_FAIL,
            payload:error.response.data.message,
        });
    }
    
};


//order details


export const getOrderDetails = (id)=>async(dispatch)=>{
     
    try {
        dispatch({ type:ORDER_DETAILS_REQUEST });
        const config = {
            headers:{
                'Content-Type':'application/json',
            },
        };

        const {data} = await axios.get(`https://onlinemoviebooking-backend.herokuapp.com/api/v1/order/${id}`);
        dispatch({type:ORDER_DETAILS_SUCCESS,payload:data.order});

    }catch(error){

        dispatch({
            type:ORDER_DETAILS_FAIL,
            payload:error.response.data.message,
        });
    }
    
};
//get all orders -- admin

export const getAllOrders = ()=>async(dispatch)=>{
     
    try {
        dispatch({ type:ALL_ORDERS_REQUEST });
       
        const {data}= await axios.get("https://onlinemoviebooking-backend.herokuapp.com/api/v1/admin/orders");
        dispatch({type:ALL_ORDERS_SUCCESS,payload:data.orders});

    }catch(error){
        dispatch({
            type:ALL_ORDERS_FAIL,
            payload:error.response.data.message,
        });
    }
    
};

//update order -admin


export const updateOrder = (id,order)=>async(dispatch)=>{
     
    try {
        dispatch({ type:UPDATE_ORDER_REQUEST });
        const config = {
            headers:{
                'Content-Type':'application/json',
            },
        };

        const {data}= await axios.put(`https://onlinemoviebooking-backend.herokuapp.com/api/v1/admin/order/${id}`,order,config);
        dispatch({type:UPDATE_ORDER_SUCCESS,payload:data.success});

    }catch(error){

        dispatch({
            type:UPDATE_ORDER_FAIL,
            payload:error.response.data.message,
        });
    }
    
};

//delete order -admin


export const deleteOrder = (id)=>async(dispatch)=>{
     
    try {
        dispatch({ type:DELETE_ORDER_REQUEST });

        const {data}= await axios.delete(`https://onlinemoviebooking-backend.herokuapp.com/api/v1/admin/order/${id}`);
        dispatch({type:DELETE_ORDER_SUCCESS,payload:data.success});

    }catch(error){

        dispatch({
            type:DELETE_ORDER_FAIL,
            payload:error.response.data.message,
        });
    }
    
};



export const clearErrors =()=>async (dispatch)=>{
    dispatch({type:CLEAR_ERRORS});
    };