import React, { Fragment } from 'react'
import CheckoutSteps from './CheckoutSteps';
import { useSelector } from 'react-redux';
import MetaData from '../layout/MetaData';
import "./ConfirmOrder.css";
import { Link } from 'react-router-dom';
import { Typography } from '@mui/material';

const ConfirmOrder = () => {
    const{shippingInfo,cartItems} = useSelector((state)=>state.cart);
    const{user}=useSelector((state)=>state.user);

    const subtotal = cartItems.reduce(
        (acc,item) => acc + item.quantity * item.price,
        0
    )
    const shippingCharges = subtotal > 1000 ? 0 :80;

    const tax = subtotal * 0.18;

    const totalPrice = subtotal + shippingCharges +tax;

    const address = `${shippingInfo.address},${shippingInfo.city},${shippingInfo.pincode}`;

    const proceedtoPayment = ()=>{

        const data = {
            subtotal,
            shippingCharges,
            tax,
            totalPrice,

        };
        sessionStorage.setItem("orderInfo",JSON.stringify(data));
        window.location.href="/process/payment";
    };

  return (
   <Fragment>
       <MetaData title="Confirm Ticket"/>
       <CheckoutSteps activeSteps={1}/> 
       <div className='confirmOrderPage'>
           <div>
                <div className='confirmshippingArea'>
                    <Typography>Movie Info</Typography>
                    <div className='confirmshippingAreaBox'>
                        <div>
                            <p>Name:</p>
                            <span>{user.name}</span>
                        </div>
                        <div>
                            <p>Mobile:</p>
                            <span>{shippingInfo.phoneNo}</span>
                        </div>
                        <div>
                            <p>Address:</p>
                            <span>{address}</span>
                        </div>

                    </div>
                </div>
                <div className='confirmCartItems'>
                    <Typography>Your Movie Tickets </Typography>
                    <div className='confirmCartItemsContainer'>
                        {cartItems &&
                           cartItems.map((item)=>(
                                <div key={item.movie}>
                                    <img src={item.image} alt="Movie"/>
                                    <Link to={`/movie/${item.movie}`}>
                                        {item.name}
                                    </Link>
                                    <span>
                                        {item.quantity} X ₹{item.price} ={"."}
                                        <b> ₹ {item.price * item.quantity}</b>
                                    </span>
                                    
                                </div>
                           ))}

                    </div>

                </div>
           </div>
          {/* */}
          <div>
              <div className='orderSummary'>
                  <Typography>Movie Ticket Details</Typography>
                  <div>
                      <div>
                          <p> Subtotal:</p>
                          <span> ₹{subtotal}</span>
                      </div>
                      <div>
                          <p>Booking Charges:</p>
                          <span> ₹{shippingCharges}</span>
                      </div>
                      <div>
                          <p>GST:</p>
                          <span> ₹{tax}</span>
                      </div>

                        <div className='orderSummaryTotal'>
                                <p>
                                    <b>Total:</b>
                                </p>
                                <span> ₹{totalPrice}</span>
                        </div>
                        <button className="paymentBtn" onClick={proceedtoPayment}>Proceed To Payment</button>
                  </div>
              </div>

          </div>
       </div>
   </Fragment>
  )
}

export default ConfirmOrder